import React from 'react';
import type { Player } from './PlayerDashboardContainer';

interface PlayerOverviewProps {
  player?: Player;
  loading?: boolean;
}

const PlayerOverview: React.FC<PlayerOverviewProps> = ({ player, loading }) => {
  if (loading || !player) {
    // Skeleton loader
    return (
      <div className='bg-slate-800 rounded-lg p-6 shadow-md mb-6 animate-pulse'>
        <div className='flex items-center space-x-4'>
          <div className='w-16 h-16 bg-gradient-to-br from-blue-500 to-purple-600 rounded-full flex items-center justify-center'>
            <div className='w-10 h-10 bg-slate-700 rounded-full' />
          </div>
          <div>
            <div className='h-6 w-32 bg-slate-700 rounded mb-2'></div>
            <div className='h-4 w-24 bg-slate-700 rounded mb-1'></div>
            <div className='h-3 w-16 bg-slate-700 rounded'></div>
          </div>
        </div>
        <div className='mt-4 grid grid-cols-2 md:grid-cols-4 gap-4'>
          {[...Array(4)].map((_, i) => (
            <div key={i}>
              <div className='text-slate-400 text-xs'>�0</div>
              <div className='h-6 w-12 bg-slate-700 rounded'></div>
            </div>
          ))}
        </div>
        <div className='mt-4 flex space-x-3'>
          <div className='h-10 w-24 bg-blue-700 rounded-lg'></div>
          <div className='h-10 w-24 bg-slate-700 rounded-lg'></div>
        </div>
      </div>
    );
  }
  return (
    <div className='bg-slate-800 rounded-lg p-6 shadow-md mb-6'>
      <div className='flex items-center space-x-4'>
        <div className='w-16 h-16 bg-gradient-to-br from-blue-500 to-purple-600 rounded-full flex items-center justify-center text-2xl font-bold text-white'>
          {player.name[0]}
        </div>
        <div>
          <h2 className='text-2xl font-bold text-white'>{player.name}</h2>
          <div className='text-slate-400 text-sm'>
            {player.team} â€¢ {player.position}
          </div>
          <div className='text-slate-400 text-xs mt-1'>{player.sport}</div>
        </div>
      </div>
      <div className='mt-4 grid grid-cols-2 md:grid-cols-4 gap-4'>
        <div>
          <div className='text-slate-400 text-xs'>Games</div>
    <div className='text-white text-lg font-semibold'>{player.season_stats?.games_played ?? '-'}</div>
        </div>
        <div>
          <div className='text-slate-400 text-xs'>Hits</div>
          <div className='text-white text-lg font-semibold'>{player.season_stats?.hits ?? '-'}</div>
        </div>
        <div>
          <div className='text-slate-400 text-xs'>HR</div>
          <div className='text-white text-lg font-semibold'>{player.season_stats?.home_runs ?? '-'}</div>
        </div>
        <div>
          <div className='text-slate-400 text-xs'>AVG</div>
          <div className='text-white text-lg font-semibold'>
            {player.season_stats?.batting_average ? player.season_stats.batting_average.toFixed(3) : '-'}
          </div>
        </div>
      </div>
      {/* Quick actions, e.g., add to favorites, share, etc. */}
      <div className='mt-4 flex space-x-3'>
        <button className='bg-blue-600 hover:bg-blue-700 text-white px-4 py-2 rounded-lg transition-colors'>
          Favorite
        </button>
        <button className='bg-slate-700 hover:bg-slate-600 text-white px-4 py-2 rounded-lg transition-colors'>
          Share
        </button>
      </div>
    </div>
  );
};

export default PlayerOverview;
